import React, { useEffect, useState } from "react";
import LessonsComponent from "./LessonsComponent";
import { courseLessonsOperations } from "app/store/state/active-course-lessons";
import { useDispatch, useSelector } from "react-redux";
import "./lessons.scss";
import { courseModulesOperations } from "app/store/state/active-course-modules";

function Lessons(props) {
  const activeCourseLessonsData = useSelector(
    (state) => state.activeCourseLessons.data
  );
  const activeCourseModulesData = useSelector(
    (state) => state.activeCourseModules.data
  );
  const tinyMceApiKey = useSelector((state) => state.app.tinyMceApiKey);
  const dispatch = useDispatch();
  const [localCourseLessonsData, setLocalCourseLessonsData] = useState([]);
  const [localCourseModulesData, setLocalCourseModulesData] = useState([]);
  const [orderModalOpen, setOrderModalOpen] = useState(false);
  const [activeCourseId, setActiveCourseId] = useState(props.match.params.id);
  const [isModified, setModifiedFlag] = useState(false);

  useEffect(() => {
    setModifiedFlag(false);
    setLocalCourseLessonsData(activeCourseLessonsData);
  }, [activeCourseLessonsData]);

  useEffect(() => {
    setLocalCourseModulesData(activeCourseModulesData);
  }, [activeCourseModulesData]);

  useEffect(() => {
    const courseId = props.match.params.id;
    setActiveCourseId(courseId);
    dispatch(courseLessonsOperations.loadLessons(courseId));
    dispatch(courseModulesOperations.loadModules(courseId));
    // eslint-disable-next-line
  }, [props.match.params.id]);

  const addLesson = (lesson) => {
    dispatch(
      courseLessonsOperations.addNewLessonDetails(activeCourseId, lesson)
    );
  };

  const updateLesson = (lesson) => {
    dispatch(courseLessonsOperations.saveLessonDetails(activeCourseId, lesson));
  };

  const deleteLesson = (lessonId) => {
    dispatch(
      courseLessonsOperations.deleteLessonDetails(activeCourseId, lessonId)
    );
  };

  const toggleOrderingModal = () => {
    setOrderModalOpen(!orderModalOpen);
  };

  const updateLessonSequence = (lessons) => {
    dispatch(
      courseLessonsOperations.saveLessonSequenceDetails(activeCourseId, lessons)
    );
  };

  return (
    <LessonsComponent
      localCourseLessonsData={localCourseLessonsData}
      localCourseModulesData={localCourseModulesData}
      isModified={isModified}
      tinyMceApiKey={tinyMceApiKey}
      orderModalOpen={orderModalOpen}
      updateLessonSequence={updateLessonSequence}
      toggleOrderingModal={toggleOrderingModal}
      addLesson={addLesson}
      updateLesson={updateLesson}
      deleteLesson={deleteLesson}
    />
  );
}

export default Lessons;
