import React from "react";
import Tab from "react-bootstrap/Tab";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Nav from "react-bootstrap/Nav";
import Button from "react-bootstrap/Button";
import LessonForm from "./components/LessonForm";
import LessonDetails from "./components/LessonDetails";
import LessonOrderModal from "./components/LessonOrderModal";

function LessonsComponent({
  localCourseLessonsData,
  localCourseModulesData,
  tinyMceApiKey,
  addLesson,
  updateLesson,
  deleteLesson,
  orderModalOpen,
  toggleOrderingModal,
  updateLessonSequence,
}) {
  return (
    <div id="cfg_lessons" className="page p-0">
      <LessonOrderModal
        visible={orderModalOpen}
        lessons={localCourseLessonsData}
        onOk={updateLessonSequence}
        onCancel={toggleOrderingModal}
      />
      <div className="p-3 bg-light lessons-header">
        <div className="lesson-count">
          <h6 className="m-0">
            Lesson Count: <b>{localCourseLessonsData.length}</b>
          </h6>
        </div>
        <div className="lessons-action-panel">
          <Button
            type="submit"
            variant="primary"
            className="ml-2 float-right font-weight-bold"
            size="sm"
            onClick={toggleOrderingModal}
          >
            Order Lessons
          </Button>
        </div>
      </div>
      <div className="p-3">
        <Tab.Container id="left-tabs-example" defaultActiveKey="addNewLesson">
          <Row>
            <Col xs={2} className="d-md-none">
              <Nav className="flex-column">
                <Nav.Item>
                  <Nav.Link
                    eventKey="addNewLesson"
                    className="ellipsis clamp-1"
                  >
                    <Button variant="secondary">+</Button>
                  </Nav.Link>
                </Nav.Item>
                {localCourseLessonsData.map((lesson) => (
                  <Nav.Item key={`lesson-${lesson.id}`}>
                    <Nav.Link eventKey={lesson.id}>
                      <Button variant="secondary">{lesson.lessonOrder}</Button>
                    </Nav.Link>
                  </Nav.Item>
                ))}
              </Nav>
            </Col>
            <Col md={3} xl={2} className="d-none d-md-block">
              <Nav variant="pills" className="flex-column">
                <Nav.Item>
                  <Nav.Link
                    eventKey="addNewLesson"
                    className="ellipsis clamp-1"
                  >
                    Add new lesson
                  </Nav.Link>
                </Nav.Item>
                {localCourseLessonsData.map((lesson) => (
                  <Nav.Item key={`lesson-${lesson.id}`}>
                    <Nav.Link
                      eventKey={lesson.id}
                      className="ellipsis clamp-2 position-relative"
                    >
                      {`${lesson.lessonOrder}. ${lesson.title}`}
                    </Nav.Link>
                  </Nav.Item>
                ))}
              </Nav>
            </Col>
            <Col xs={9} xl={10}>
              <Tab.Content>
                <Tab.Pane eventKey="addNewLesson">
                  <LessonForm
                    mode="add"
                    onAdd={addLesson}
                    tinyMceApiKey={tinyMceApiKey}
                    modules={localCourseModulesData}
                  />
                </Tab.Pane>
                {localCourseLessonsData.map((lesson) => (
                  <Tab.Pane
                    eventKey={lesson.id}
                    key={`lesson-pane-${lesson.id}`}
                  >
                    <LessonDetails
                      key={`lesson-${lesson.id}`}
                      lesson={lesson}
                      onUpdate={updateLesson}
                      deleteLesson={deleteLesson}
                      modules={localCourseModulesData}
                    />
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
      </div>
    </div>
  );
}

LessonsComponent.propTypes = {};

export default LessonsComponent;
