import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { SortableContainer, SortableElement } from "react-sortable-hoc";
import arrayMove from "array-move";
import "./lesson-order.scss";

function LessonOrderModal({ visible, onOk, onCancel, lessons = [] }) {
  const [updatedLessons, setUpdatedLessons] = useState(lessons);

  const SortableItem = SortableElement(({ lesson, order }) => (
    <div className="font-weight-bold d-flex m-2 p-2 draggable-lesson-item align-items-center">
      <div className="order mr-2">{order}</div>
      <div>{lesson.title}</div>
    </div>
  ));

  const SortableList = SortableContainer(({ list }) => {
    return (
      <div>
        {list.map((lesson, index) => (
          <SortableItem
            key={`item-${lesson.id}`}
            index={index}
            lesson={lesson}
            order={index + 1}
          />
        ))}
      </div>
    );
  });

  const onSortEnd = ({ oldIndex, newIndex }) => {
    if (oldIndex !== newIndex) {
      setUpdatedLessons(arrayMove(updatedLessons, oldIndex, newIndex));
    }
  };

  const saveSequence = (e) => {
    e.stopPropagation();
    onOk(updatedLessons);
  };

  useEffect(() => {
    setUpdatedLessons(lessons);
  }, [lessons]);

  return (
    <Modal
      show={visible}
      centered
      size="lg"
      onHide={onCancel}
      id="cfg_lesson_order_modal"
    >
      <Modal.Body>
        <SortableList list={updatedLessons} onSortEnd={onSortEnd} />
        <div className="pt-3 text-right">
          <Button variant="success" className="ml-2" onClick={saveSequence}>
            Save Sequence
          </Button>
          <Button variant="light" className="ml-2" onClick={onCancel}>
            Cancel
          </Button>
        </div>
      </Modal.Body>
    </Modal>
  );
}

LessonOrderModal.propTypes = {
  visible: PropTypes.bool,
  onCancel: PropTypes.func,
  onOk: PropTypes.func,
  lessons: PropTypes.array,
};

export default LessonOrderModal;
