import React, { useState } from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import DeleteConfirmation from "app/views/common/components/DeleteConfirmation";
import {
  faBook,
  faSitemap,
  faFileCode,
  faListUl,
  faQuoteLeft,
  faPencilAlt,
  faTrash,
  faEye,
  faFile,
} from "@fortawesome/free-solid-svg-icons";
import LessonForm from "../LessonForm";

function LessonDetails({ lesson, onUpdate, deleteLesson, modules }) {
  const {
    title,
    slug,
    excerpt,
    videoUrl,
    content,
    lessonOrder,
    sectionId,
    isPreviewEnabled,
    id,
  } = lesson;
  const [editMode, setEditMode] = useState(false);
  const [deleteMode, setDeleteMode] = useState(false);

  const toggleEditMode = () => {
    setEditMode(!editMode);
  };
  const toggleDeleteMode = () => {
    setDeleteMode(!deleteMode);
  };
  const handleDeleteLesson = () => {
    deleteLesson(id);
  };

  const moduleData = modules.filter((mod) => sectionId === mod.id);
  const hasValidModuleData = moduleData.length > 0;

  return (
    <div className="lesson-details">
      <DeleteConfirmation
        visible={deleteMode}
        onCancel={toggleDeleteMode}
        onOk={handleDeleteLesson}
        message={`Are you sure you want to delete this lesson?`}
      />
      <div className="text-right p-1">
        <Button
          variant="warning"
          className="text-right"
          onClick={toggleEditMode}
        >
          <FontAwesomeIcon icon={faPencilAlt} className="mr-2" />
          Edit
        </Button>
        <Button
          variant="danger"
          className="text-right ml-2"
          onClick={toggleDeleteMode}
          disabled={deleteMode}
        >
          <FontAwesomeIcon icon={faTrash} className="mr-2" />
          Delete
        </Button>
      </div>

      {editMode ? (
        <LessonForm
          mode="update"
          onUpdate={onUpdate}
          lesson={lesson}
          modules={modules}
        />
      ) : (
        <Card className="border-0">
          <Card.Body>
            <div className="text-muted">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faBook} className="mr-2" />
                  Title
                </strong>
                <div>{title || "Not specified"}</div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faFile} className="mr-2" />
                  Module
                </strong>
                <div>
                  {hasValidModuleData ? moduleData[0].name : "Not specified"}
                </div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faListUl} className="mr-2" />
                  Lesson Order
                </strong>
                <div>{lessonOrder}</div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faSitemap} className="mr-2" />
                  Slug
                </strong>
                <div>{slug || "Not specified"}</div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faQuoteLeft} className="mr-2" />
                  Excerpt
                </strong>
                <div>{excerpt || "Not specified"}</div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faQuoteLeft} className="mr-2" />
                  Video URL
                </strong>
                <div>{videoUrl || "Not specified"}</div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faEye} className="mr-2" />
                  Preview Enabled
                </strong>
                <div className="mt-1">
                  <label className="switch mb-0">
                    <input
                      type="checkbox"
                      checked={isPreviewEnabled}
                      disabled
                    />
                    <span className="slider round"></span>
                  </label>
                </div>
              </div>
            </div>
            <div className="text-muted pt-3">
              <div className="border-bottom mb-0 pb-3">
                <strong className="d-block text-gray-dark">
                  <FontAwesomeIcon icon={faFileCode} className="mr-2" />
                  Content
                </strong>
                <div id="editor-disabled">
                  {content ? (
                    <div className="bg-dark p-3 mt-3 mb-0 text-white rounded lesson-content">
                      {content}
                    </div>
                  ) : (
                    "Not specified"
                  )}
                </div>
              </div>
            </div>
          </Card.Body>
        </Card>
      )}
    </div>
  );
}

LessonDetails.propTypes = {};

export default LessonDetails;
